// Calcular fee din√°mico basado en rating
export function calculateDynamicFee(rating: number): number {
  if (rating >= 1 && rating <= 2) {
    return 70 // 30% discount (0.70%)
  }
  return 100 // Standard fee (1.00%)
}

// Calcular fee standard de Uniswap v3
export function calculateStandardFee(): number {
  return 3000 // 0.30% en basis points (3000 = 0.30%)
}
